import React, { Component } from "react"
import { Link } from "gatsby"
import { connect } from "react-redux"
import wrapup from "../components/wrapup.module.scss"

class WrapUp extends Component {
  render() {
    const events = this.props.events.map((event, i) => {
      return (
        <div className={wrapup.card}>
          <hr />
          <p>{event.title}</p>
          <p>{event.category}</p>
          <p>{event.longestDuration}</p>
          <p>{event.highestCount}</p>
          <p>{event.averageCount}</p>
          <p>{event.lowestCount}</p>
        </div>
      )
    })
    return <div className="content">{events}</div>
  }
}

function mapStateToProps(state) {
  return {
    categories: state.categories,
    events: state.events,
  }
}

export default connect(mapStateToProps)(WrapUp)

